.attraction {
  overflow: hidden;
  margin-bottom: 2%;
  font-family: Abhaya Libre ExtraBold;
  .titleBlock {
    width: 100%;
    text-align: center;
    .title {
      display: inline-block;
      p {
        font-size: 3vw;
        color: #325e65;
      }
      hr {
        border: 1px solid #325e65;
        background: #325e65;
      }
    }
  }
  .mainPart {
    font-size: 1.5vw;
    margin-left: auto;
    margin-right: auto;
    width: 70%;
    margin-left: 15%;
    margin-top: 2%;
    color: #325e65;
  }
  .carouselAttraction {
    display: flex;
    width: 375%;
    justify-content: space-between;
    margin-top: 2%;
    margin-left: 26%;
    div {
      width: 47vw;
      img {
        width: 47vw;
      }
      hr {
        margin-top: 3%;
        border: 0.5px solid #325e65;
        background: #325e65;
        width: 100%;
      }
      p {
        font-style: italic;
        margin-top: 1%;
        font-size: 1.4vw;
        text-align: center;
        color: #325e65;
      }
    }
  }

  .arrowAttraction {
    position: absolute;
    display: flex;
    flex-direction: row;
    margin-top: 15%;
    margin-left: 23%;
    width: 24%;
    z-index: 2;
    img {
      width: 7%;
      &:nth-of-type(1) {
        transform: rotate(-180deg);
      }
      &:nth-of-type(2) {
        margin-left: 207%;
      }
      &:hover {
        cursor: pointer;
      }
    }
  }
  .buttonBlock {
    width: 100%;
    text-align: center;
    .buttonAttraction {
      margin-top: 2%;
      width: 55%;
      height: 50px;
      text-align: center;
      background-color: #426166;
      font-size: 1.5vw;
      color: white;
      border-radius: 5px;
      transition: background-color 0.5s ease 0s;

      &:hover {
        background-color: #355257;
        transition: all 0.5s ease 0s;
      }
    }
  }
}
